import React from "react";

import "./Footer.css";

const Footer = () => {
  return (
    <div className="footer bg-secondary d-flex justify-content-center">
      <section className="layout-social py-4">
        <a className="layout-icon text-light px-2" href="">
          <i className="fab fa-youtube"></i>
        </a>
        <a className="layout-icon text-light px-2" href="">
          <i className="fab fa-facebook"></i>
        </a>
        <a className="layout-icon text-light px-2" href="">
          <i className="fab fa-linkedin"></i>
        </a>
      </section>
    </div>
  );
};

export default Footer;
