import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Home from "./Pages/Home/Home";
import About from "./Pages/About/About";
import Video from "./Components/Video/Video";
import Contact from "./Pages/Contact/Contact";
import Admin from "./Adm/Admin/Admin";
import AddVideo from "./Adm/AddVideo/AddVideo";
import UpdateVideo from "./Adm/UpdateVideo/UpdateVideo";
import DeleteVideo from "./Adm/DeleteVideo/DeleteVideo";

const Routing = () => {
  return (
    <div>
      <div>
        <BrowserRouter>
          <Routes>
            <Route index element={<Home />} />
            <Route path="/home" element={<Home />} />
            <Route path="/about" element={<About />} />
            <Route path="/video" element={<Video />} />
            <Route path="/contact" element={<Contact />} />
            <Route path="/admin" element={<Admin />} />
            <Route path="/addvideo" element={<AddVideo />} />
            <Route path="/updatevideo/:id" element={<UpdateVideo />} />
            <Route path="/deletevideo" element={<DeleteVideo />} />
          </Routes>
        </BrowserRouter>
      </div>
    </div>
  );
};

export default Routing;
