import { Component } from "react";
import logo from "../../Assets/img/jozaylogo_white.png";

import "./Navbar.css";

class Navbar extends Component {
  // initialize state
  state = { clicked: false };
  handleClick = () => {
    this.setState({ clicked: !this.state.clicked });
  };
  //Render
  render() {
    return (
      <>
        <div className="navbar-container">
          <nav>
            <a href="index.html">
              <img className="logo" src={logo} alt="Garage Jazz Logo" />
              <span className="nav-title">Garage Jazz</span>
              <i className="fas fa-music mx-2"></i>
              <br className="d-sm-none" />
              <span className="nav-desc">Isaiah &amp; Josiah Schu</span>
            </a>
            <div className="nav-links">
              <ul
                id="navbar"
                className={this.state.clicked ? "#navbar active" : "#navbar"}
              >
                <li>
                  <a className="active" href="home">
                    home
                  </a>
                </li>
                <li>
                  <a href="/about">about</a>
                </li>
                <li>
                  <a href="/video">video</a>
                </li>
                <li>
                  <a href="/contact">contact</a>
                </li>
                <li>
                  <a href="/admin">admin</a>
                </li>
              </ul>
            </div>
            <div id="mobile" onClick={this.handleClick}>
              <i
                id="bar"
                className={this.state.clicked ? "fas fa-times" : "fas fa-bars"}
              ></i>
              {/* <i className="fas fa-bars"></i>
            <i className="fas fa-times"></i> */}
            </div>
          </nav>
        </div>
      </>
    );
  }
}

export default Navbar;
